export default {
	props: {
		item: Object,
		index: Number
	},
	methods: {
		openEvent(item) {
			console.log(item)
			switch (item.clicktype) {
				case 'navigateTo':
					if (item.url) {
						uni.navigateTo({
							url: item.url
						});
					}
					break;
				case 'switchTab':
					if (item.url) {
						uni.switchTab({
							url: item.url
						})
					}
					break;
			}
		}
	}
};
